












































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import Header from '@/components/layout/Header.vue';
import InfoSide from '@/components/InfoSide.vue';
import Stepper from '@/components/utilities/Stepper.vue';
import FirstStep from '@/modules/verificationLevels/components/levelOne/FirstStep.vue'
import SecondStep from '@/modules/verificationLevels/components/levelOne/SecondStep.vue'
import ThirdStep from '@/modules/verificationLevels/components/levelOne/ThirdStep.vue'
import SvgIcon from '@/components/utilities/SvgIcon.vue';
import Calculator from '@/modules/remittance/components/Calculator.vue';

@Component({
    components: {
        Header,
        InfoSide,
        Stepper,
        FirstStep,
        SecondStep,
        ThirdStep,
        SvgIcon,
        Calculator
    },
})
export default class LevelOne extends Vue {


    $refs : any ={}
    steps= [
        { component: 'FirstStep', label: 'Beneficiarios' },
        { component: 'SecondStep', label: 'Montos' },
        { component: 'ThirdStep', label: 'Pagos' },
        { component: 'ThirdStep', label: 'Comprobante' },
    ]
    currentComponent = this.steps[0].component
    currentStep = 1;


    nextStep(){
        this.$refs.step.moveStep(this.currentStep+1);
        this.currentStep += 1;
        this.currentComponent= this.steps[this.currentStep-1].component;
    }

    prevStep(){
        this.$refs.step.moveStep(this.currentStep-1);
        this.currentStep -= 1;
        this.currentComponent= this.steps[this.currentStep-1].component;
    }

}
